import React from "react"
import { Link } from "gatsby"

import Layout from "../../components/layout"
import "./note-list.scss";

const DailyNotes = ({ data }) => (
  <Layout>
    <div>
      {data.allMarkdownRemark.edges.map(post => {
        return (
          <div className="border-anim2" key={post.node.id}>
            <div className="border-anim-content">

              <Link
                key={post.node.id}
                to={post.node.frontmatter.path}>
                {post.node.frontmatter.title}
              </Link>

            </div>
            <div className="border-anim2-edge border-anim2-left"></div>
            <div className="border-anim2-edge border-anim2-top"></div>
            <div className="border-anim2-edge border-anim2-right"></div>
            <div className="border-anim2-edge border-anim2-bottom"></div>
          </div>
          // <div key={post.node.id}>
          // </div>
        )
      }
      )}
    </div>
  </Layout>
)
export const pageQuery = graphql`
  query DailyNotesQuery {  
    allMarkdownRemark(
      limit: 10 
      sort: {fields: frontmatter___date order: DESC}
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            date
          }
        }
      }
    }
  }
`
export default DailyNotes
